import React from 'react';

import {Helmet} from 'react-helmet';

import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import theme from './theme.js';

import NavigationBar from './components/NavigationBar/NavigationBar.js';

import Projects from './pages/Projects/Page.js';
import About from './pages/About/Page.js';
// import Contact from './pages/Contact/Page.js';


const pages = {
  projects: {title: 'Projects', content: <Projects/>},
  about: {title: 'About', content: <About/>},
//  contact: {title: 'Contact', content: <Contact/>}
};

const defaultPage = 'projects';

class App extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: defaultPage,
    }
  }


  /***** Setup *****/
  componentDidMount() {
    this.handlePageChange();
    window.addEventListener('popstate', this.handlePageChange.bind(this));

    // Scroll data
    this.setScrollListener();
    document.documentElement.dataset.scroll = 0;
  }

  componentDidUpdate() {
    this.setScrollListener();
  }


  setScrollListener() {
    const root = document.getElementById('page-container');
    this.handleScroll(root);
    root.addEventListener('scroll', () => this.handleScroll(root));
  }

  handleScroll(root) {
    // Prevent horizontal scroll
    if (root.scrollLeft > 0) {root.scrollLeft = 0;}

    // Record scroll data for css
    const position = root.scrollTop;
    document.documentElement.dataset.scroll = position;
  }



  /***** Navigation *****/
  handlePageChange() {
    // Load different pages on startup depending on URL
    let page = window.location.pathname.slice(1);
    if (page !== '') {
      if (page.endsWith('.html')) {
	page = page.slice(0, page.length - 5);
      }
      if (Object.keys(pages).includes(page) && page !== this.state.activeTab) {
	this.handleTabChange(page);
      }
    } else {
      this.handleTabChange(defaultPage);
    }
  }

  handleTabChange(key) {
    this.setState({activeTab: key});
    document.getElementById('page-container').scrollTop = 0;
    return false;
  }




  /***** Rendering *****/
  renderNavigationBar() {
    const tabs = Object.entries(pages).map(([key, page]) => ({
	id: key,
	label: page.title
    }));

    return (
      <NavigationBar
	tabs={tabs}
	activeTab={this.state.activeTab}
	onTabChange={key => this.handleTabChange(key)}
      />
    );
  }

  renderPage(key) {
    return pages[key].content;
  }

  render() {
    const {activeTab} = this.state;
    return <>
      <Helmet>
          <title>{'Samuel J. Li — ' + pages[activeTab].title}</title>
      </Helmet>
      <MuiThemeProvider theme={theme}>
	{this.renderNavigationBar()}
	{this.renderPage(activeTab)}
      </MuiThemeProvider>
    </>;
  }
}

export default App;
